/* Root Style Adjustments */
:root {
    --background-color: #1A1A1A; /* Darker shade for more focus on content */
    --primary-text-color: #FFFFFF;
    --secondary-text-color: #AAAAAA; /* Light grey for less emphasis text */
    --accent-color: #E6E6E6; /* Soft white for important buttons or links */
  }
  
  /* General Body and Typography Settings */
  body {
    font-family: 'Roboto', 'Helvetica', 'Arial', sans-serif;
    background-color: var(--background-color);
    color: var(--primary-text-color);
    margin: 0;
    padding: 0;
    line-height: 1.5;
  }
  
  h4, h5 {
    color: var(--accent-color);
    margin-bottom: 20px;
  }
  
  a {
    color: var(--accent-color);
    text-decoration: none; /* No underlines for a cleaner look */
  }
  
  a:hover {
    text-decoration: underline; /* Underline on hover for better user interaction feedback */
  }
  
  /* Portfolio Layout Enhancements */
  .Portfolio {
    display: flex;
    flex-direction: column;
    gap: 40px; /* Space between categories */
    padding: 40px;
  }
  
  iframe {
    max-width: 100%;
    border: none; /* Remove default border */
    box-shadow: 0 4px 8px rgba(0,0,0,0.1); /* Subtle shadow for depth */
  }
  
  /* Responsive Grid for Videos */
  @media (min-width: 600px) {
    .Portfolio {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)); /* Responsive grid layout */
      gap: 20px;
    }
  }
  
  /* Enhanced Typography Settings */
body, h4, h5 {
    font-family: 'Open Sans', sans-serif; /* More modern-looking font */
    font-weight: 400;
  }
  
  h4 {
    font-size: 1.5em; /* Larger for better visibility */
    font-weight: 600; /* Heavier weight for headings */
  }
  
  /* Enhanced Hover States */
  a:hover {
    color: var(--link-hover-color); /* More noticeable hover effect */
    transition: color 0.3s; /* Smooth transition for color change */
  }
  
  /* Improved Layout and Spacing */
  .Portfolio {
    padding: 50px 40px; /* Increased padding for better spacing */
  }
  
  /* Subtle animations for links */
  a {
    transition: transform 0.3s ease-out;
  }
  
  a:hover {
    transform: translateY(-2px); /* Slight raise effect on hover */
  }
  