/* Root and Color Scheme */
:root {
  --bg-color: #121212; /* Dark background */
  --text-color: #FFFFFF; /* Bright text for high contrast */
  --accent-color: #4A90E2; /* Blue accent color */
  --link-color: #BB86FC; /* Purple color for links */
}

/* Global Styles */
body {
  font-family: 'Roboto', sans-serif;
  color: var(--text-color);
  background-color: var(--bg-color);
  margin: 0;
  font-size: 16px; /* Base font size */
  line-height: 1.6;
}

a {
  color: var(--link-color);
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

h1, h2, h3, h4, h5, h6 {
  color: var(--accent-color);
}

/* App Component Layout */
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 20px;
  width: 100%;
}

/* Navbar Component Styles */
.Navbar {
  width: 100%;
  background-color: #333; /* Darker shade for navbar */
  color: var(--text-color);
}

.Toolbar {
  display: flex;
  justify-content: space-between;
  padding: 0 10%;
}

.Button {
  color: var(--text-color);
}

.Button:hover {
  background-color: var(--accent-color);
  color: var(--bg-color);
}

/* Box Styling for Layout */
.Box {
  transition: transform 0.3s ease-in-out;
}

.Box:hover {
  transform: translateY(-5px);
}

/* Media Queries for Responsiveness */
@media (max-width: 768px) {
  .Toolbar {
    flex-direction: column;
  }
}

main {
  scroll-behavior: smooth;
}

.Box {
  min-height: 100vh; /* Each section takes at least the full height of the viewport */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
